.root_wrap {
    width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 15px 10px;
    box-sizing: border-box;
    margin-right: 50px;
    border-radius: 5px;
    border:1px  solid  #363636;
    background: #424242 none repeat scroll 0 0;
}
button, input {
    padding: 0;
    margin: 0;
    outline: none;
    border: none;
}


